@media screen and (max-width: 639px) {
    .all-services {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        overflow-y: auto;

        margin-bottom: 10px;
        h1 {
            animation: disparition 2s ease-in;
            display: none;
            top: 25%;
            left: 35%;
            position: fixed;
            width: 100%;
            text-align: center;
            padding: 0;
            font-size: 1.75em;
        }
        .not-actived {
            width: 100%;
            display: flex;
        }
        .actived {
            display: none;
            width: 0%;
            transition: all 1s;
        }
        .isnt-active {
            width: 0;
            display: none;
        }
        .is-active {
            transition: all 3s;
            width: 100%;
            display: flex;
        }

        .services {
            margin-top: 60px;
            animation: apparition 2s ease-in;
            overflow-y: auto;
            height: 100%;
            display: flex;
            flex-wrap: wrap;
            width: 100%;
            justify-content: space-around;
        }

        .service {
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 50%;
            margin-bottom: 12%;
            height: 250px;
            justify-content: space-around;
            img {
                width: 61.73%;
                // height: 40%;
                object-fit: cover;
            }
            .information {
                width: 90%;
                display: flex;
                flex-direction: column;
                align-items: center;
                text-align: center;
                height: 33%;
                justify-content: space-between;
                h3 {
                    font-size: 0.75em;
                    font-family: "Kodchasan-bold";
                    color: #fee8c0;
                    text-align: center;
                }
                p {
                    font-size: 0.75em;
                    color: #fefefe;
                    font-family: "Kodchasan-italic";
                }
            }
        }
        .original-class {
            margin-top: 42px;
        }
        .modified-class {
            margin-top: 42px;
            top: 0;
        }
    }
}
@media screen and (min-width: 640px) and (max-width: 1023px) {
    .all-services {
        width: 72%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        overflow-y: auto;

        margin-bottom: 10px;
        h1 {
            font-size: 2.5em;
            animation: disparition 2s ease-in;
            display: none;
            top: 45%;
            left: 45%;
            position: fixed;
            width: 100%;
            text-align: center;
            padding: 0;
        }
        .not-actived {
            width: 100%;
            display: flex;
            left: 55%;
        }
        .actived {
            left: 55%;
            display: none;
            width: 0%;
            transition: all 1s;
        }
        // .isnt-active {
        //     width: 0;
        //     display: none;
        // }
        // .is-active {
        //     transition: all 3s;
        //     width: 100%;
        //     display: flex;
        // }
        .services {
            height: 100vh;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            .one {
                animation: diag-haut 3s ease-in;
            }
            .two {
                animation: diag-haut-2 3s ease-in;
            }
            .three,
            .five {
                animation: left-to-right 3s ease-in;
            }
            .four,
            .six {
                animation: right-to-left 3s ease-in;
            }
            .seven {
                animation: diag-bas 3s ease-in;
            }
            .height {
                animation: diag-bas-2 3s ease-in;
            }
            .service {
                width: 40%;
                display: flex;
                flex-direction: column;
                height: 245px;
                align-items: center;
                justify-content: space-around;
                margin-bottom: 25px;
                margin-top: 40px;
                img {
                    width: 57%;
                }
                .information {
                    width: 90%;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    text-align: center;
                    height: 33%;
                    justify-content: space-between;
                    h3 {
                        margin-bottom: 8px;
                        font-size: 1em;
                        font-family: "Kodchasan-bold";
                        color: #fee8c0;
                        text-align: center;
                    }
                    p {
                        font-size: 0.8em;
                        color: #fefefe;
                        font-family: "Kodchasan-italic";
                    }
                }
            }
          
        }
    }
}
