.div01 {
    background: #333f4f;
    display: flex;
    align-items: center;
    .navigation {
        width: 100%;
        ul {
            width: 100%;
            align-items: center;
            li{
                color: #FFFFFF;
                font-family: "Quango";
            }
        }
    }
}

// mobile
@media screen and (max-width: 639px) {
    .languages,
    .navigation,
    .footer,
    .social {
        display: none;
    }
    .div01 {
        position: fixed;
        width: 100%;
        z-index: 3;
        height: 50px;
        border-bottom: solid 3px #dbbe6e;
        .logo {
            width: 13.85%;
            // height: 3.075vh;
            margin-left: 2.814%;
            object-fit: cover;
        }
    }
}
