@keyframes slideLeft {
  0%{
   right: 100%;
  }
  100%{
    right: 0;
  }
}
@media screen and (max-width: 639px) {
  .contact{
    animation: slideLeft 1.5s ease-in;
    position: relative;
  }
  .catch-phrase {
    animation: slideLeft 1.4s ease-in;
    position: relative;
  }
  .identity-contact{
    position: relative;
    animation: slideFromRightBis 1.7s ease-in;
  }
  .description {
    position: relative;
    animation: slideFromRightBis 1.6s ease-in;
  }
  .computer,
  .two,
  .four,
  .six,
  .height {
    animation: slideFromRight 3s forwards; /* Durée de l'animation et direction */
  }
  .mercedes {
    animation: slideFromRightBis 3s forwards; /* Durée de l'animation et direction */
  }
  .text-about-me,
  .one,
  .three,
  .five,
  .seven {
    animation: slideFromLeft 3s forwards; /* Durée de l'animation et direction */
  }
}

@keyframes slideFromRight {
  0% {
    left: 100%;
  }
  100% {
    left: 0; /* Déplace l'image complètement à gauche */
  }
}
@keyframes slideFromRightBis {
  0% {
    left: 100%;
  }
  100% {
    left: 15%; /* Déplace l'image complètement à gauche */
  }
}
@keyframes slideFromRight2 {
  0% {
    right: 100%;
  }
  100% {
    right: 0; /* Déplace l'image complètement à gauche */
  }
}
@keyframes slideFromLeft {
  0% {
    right: 100%;
  }
  100% {
    right: 0; /* Déplace l'image complètement à gauche */
  }
}
@keyframes slideFromLeft2 {
  0% {
    left: 100%;
  }
  100% {
    left: 15%; /* Déplace l'image complètement à gauche */
  }
}
@keyframes slideFromLeft3 {
  0% {
    left: 100%;
  }
  100% {
    left: 0%; /* Déplace l'image complètement à gauche */
  }
}
@keyframes slideFromTop {
  0% {
    bottom: 100%;
  }
  100% {
    bottom: 0%; /* Déplace l'image complètement à gauche */
  }
}
@keyframes disparition {
  0% {
    display: flex;
  }
  100% {
    opacity: 0;
    display: none;
  }
}

@keyframes apparition {
  0% {
    display: none;
  }
  100% {
    display: flex;
  }
}
@keyframes haut {
  0% {
    transform: translateY(-3000px);
  }
  100%{
    transform: translateY(0px);
  }
}
@keyframes bas {
  0% {
    transform: translateY(3000px);
  }
  100%{
    transform: translateY(0px);
  }
}
@keyframes diag-haut {
  0% {
    transform: translate(-1000px, -1000px);
  }
  100%{ transform: translate(0px);}
}
@keyframes diag-haut-2 {
  0% {
    transform: translate(1000px, -1000px);
  }
  100%{ transform: translate(0px);}
}
@keyframes left-to-right {
  0% {
    transform: translateX(-1000px);
  }
  100%{ transform: translateX(0px);}
}
@keyframes right-to-left {
  0% {
    transform: translateX(1000px);
  }
  100%{ transform: translateX(0px);}
}
@keyframes diag-bas {
  0% {
    transform: translate(-1000px, 1000px);
  }
  100%{ transform: translate(0px);}
}
@keyframes diag-bas-2 {
  0% {
    transform: translate(1000px, 1000px);
  }
  100%{ transform: translate(0px);}
}

@keyframes visible {
  0%{
    visibility:visible
  };
  100%{
    visibility: hidden;
  }
  
}