.hero-page {
  background: #445061;
  position: fixed;
  .div-title {
    display: flex;
    h1 {
      position: relative;
    }
    .computer {
      position: relative;
      object-fit: cover;
    }
  }
}

//mobile
@media screen and (max-width: 639px) {
  .hero-page {
    background: #445061;
    height: 100vh;
    width: 100%;
    .div-title {
      height: 100%;
      flex-direction: column-reverse;
      justify-content: center;
      align-items: center;
      h1 {
       // margin-left: 15%;
        // z-index: 3;
        position: relative;
        width: 60.625%;
max-width: 200px;
        transform: translate(-22%, -125%);
      

        animation: slideFromLeft 3s ease-in-out forwards; /* Durée de l'animation et direction */
        img{
          height: 100%;
          width: 100%;
          object-fit: cover;
         
          
        }
        .title {
          width: 100%;
        }
      }
      .computer {
        position: relative;
         width: 94.375%;
        height: 57.294vh;
        object-fit: cover;
        max-width: 320px;
        // height: 57.294vh;
       
      }
    }
  }
}


// tablette
@media screen and (min-width: 640px) and (max-width: 1023px) {
  .hero-page {
    height: 100vh;
    width: 73.045%;
    right: 0;

    .div-title {
      flex-direction: column-reverse;
      height: 100%;
      justify-content: center;
      //align-items: center;
      .computer {
        animation: slideFromTop 3s ease-in-out forwards; /* Durée de l'animation et direction */
        max-width: 100%;
        max-height: 95vh;
        
       
      }
      h1 {
        height: 17vh;
        width: 54%;
        transform: translate(20%, -100%);
        animation: slideFromBottom 3s ease-in-out forwards; /* Durée de l'animation et direction */
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  @keyframes slideFromTop {
    0% {
      bottom: 100%;
    }
    100% {
      bottom: 0; /* Déplace l'image haut bas */
    }
  }
}
@keyframes slideFromBottom {
  0% {
    top: 100%;
  }
  100% {
    top: 0; /* Déplace l'image haut bas */
  }
}

//dekstop
@media screen and (min-width: 1024px) {
  .hero-page {
    width: 80.937%;
    right: 0;
    top: 0;
    height: 100%;
    .div-title {
      width: 100%;
      height: 100%;
      flex-direction: column-reverse;
      justify-content: space-around;
      align-items: center;
      h1 {
        width: 40%;
        // height: 20%;
        z-index: 2;
        // position: absolute;
        animation: slideFromBottom 3s ease-in-out forwards; /* Durée de l'animation et direction */
        transform: translate(2%, 121%);

        // height: 40vh;
        img {
          z-index: 2;
          // height: 100%;
          object-fit: cover;
          width: 60%;
        }
      }
      .computer {
        display: flex;
        // top: 0;
        // position: 1;
        z-index: 4;
        //object-fit: cover;
        // height: 100%;
        width: 65%;
         position: absolute;
        animation: slideFromLeft2 3s ease-in-out forwards; /* Durée de l'animation et direction */
      }
    }
  }
}
