@media screen and (max-width: 639px) {
    .contact-page {
        display: flex;
       overflow-y: scroll;
       scrollbar-width: none; // Masque la barre de défilement pour Firefox
       -ms-overflow-style: none; // Masque la barre de défilement pour IE et Edge
       border-radius: 15px;
       // Masque la barre de défilement pour WebKit (Chrome, Safari, etc.)
       &::-webkit-scrollbar {
           display: none;
       }
        flex-direction: column;
        justify-content: space-around;
        margin-top: 42px;
        background: #ffffff;
        height: 1200px;
        .original-class,
        .modified-class {
            top: 0;
            margin-top: 52px;
        }
        h1 {
            margin-top: 65px;
            margin-left: 8.75%;
            color: #333f4f;
            font-weight: 500;
            
        }
        .information-me {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            height: 300px;
            h2 {
                margin-left: 8.75%;
                color: #8197b8;
                font-family: "Kodchasan-regular";
                font-size: 0.8em;
            }
            .social-media-all {
                // margin-left: 8.75%;
                display: flex;
                width: 80%;
                justify-content: space-around;
                .social {
                    display: flex;
                    height: 44px;
                    width: 44px;
                    a,
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
            }
            h3 {
                margin-left: 8.75%;
                color: #333f4f;
                font-family: "Kodchasan-regular";
                font-size: 1.4em;
                .description {
                    font-weight: 100;
                    color: #ad9657;
                    font-size: 0.8em;
                }
            }
            .direct-contact {
                height: 80px;
                margin-left: 8.75%;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                a {
                    font-family: "Kodchasan-regular";
                    align-items: center;
                    display: flex;
                    p {
                        color: #333f4f;
                        margin-left: 5%;
                        font-family: "Kodchasan-regular";
                    }
                    img {
                        width: 30px;
                        height: 30px;
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 640px) and (max-width: 1023px) {
    .contact-page {
        background: #ffffff;
        overflow-y: scroll;
        scrollbar-width: none; // Masque la barre de défilement pour Firefox
        -ms-overflow-style: none; // Masque la barre de défilement pour IE et Edge
       
        // Masque la barre de défilement pour WebKit (Chrome, Safari, etc.)
        &::-webkit-scrollbar {
            display: none;
        }
        width: 73.045%;
        h1, .catch-phrase{
           animation: slideFromRight 1.8s ease-in;
           position: relative;
        }
        .slogan{
            animation: slideFromLeft  2.1s ease-in;
            position: relative;
            color: #333f4f;
           margin-bottom: 25px;
            margin-left: 10%;
        }
        h1,
        .information-me
        {
            color: #333f4f;
            margin-top: 20px;
            margin-left: 10%;
        }
        h1 {
            font-size: 2em;
            font-weight: 300;
        }
        .information-me {
            .social-media-all {
                display: none;
            }
            h2 {
                font-family: "Kodchasan-regular";
                color: #8197b8;
                font-size: 1.1em;
                font-weight: 100;
            }
            h3 {
                margin-top: 5%;
                font-family: "Kodchasan-regular";
                color: #333f4f;
                font-size: 3em;
                font-weight: 100;
                display: flex;
                flex-direction: column;
                animation: slideFromLeft 2s ease-in;
                position: relative;
                .description {
                    color: #ad9657;
                    font-size: 0.48em;
                    font-weight: 100;
                }
            }
            .direct-contact {
                animation: slideFromLeft 2s ease-in;
                position: relative;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                height: 70px;
                margin-top: 5%;
                a {
                    display: flex;
                    align-items: center;
                    img {
                        width: 25px;
                    }
                    p {
                        font-family: "Kodchasan-regular";
                        color: #333f4f;
                        margin-left: 3%;
                    }
                }
            }
        }
        .form-container {
            width: 90%;
            margin-left: 10%;
            position: relative;
            animation: slideFromBottom 2s ease-in;
            form {
               
                align-content: space-around;
                height: 450px;
                display: flex;
                flex-wrap: wrap;
                width: 95%;
                justify-content: space-between;
                .information:nth-child(2),
                .information:nth-child(4) {
                    align-items: flex-end;
                    
                    input {
                        // width: 100%;

                        border-radius: 15px 0px 15px 15px;
                    }
                }
                .information:nth-child(1),
                .information:nth-child(3) {
                   
                    input {
                        // width: 100%;

                        border-radius: 0px 15px 15px 15px;
                    }
                }
                .information:nth-child(5) {
                    width: 100%;
                }
                .information {
                    display: flex;
                    flex-direction: column;
                    width: 40%;
                    label {
                        background: #333f4f;
                        color: #ffffff;
                        font-family: "Kodchasan-regular";
                        border-radius: 15px 15px 0px 0px;
                        text-align: center;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                    .name {
                        width: 41.44%;
                        height: 25px;
                    }
                    .company,
                    .email {
                        width: 54.756%;
                        height: 25px;
                    }
                    .phone,
                    .message {
                        width: 55.25%;
                        height: 25px;
                    }

                    input,
                    textarea {
                        border: none;
                        background: #becadb;
                        height: 25px;
                        color: #ffffff;
                        padding: 2% 5% 0;
                        font-size: 1.1em;
                    }
                    input:focus {
                        outline: none; // Pour enlever la bordure
                    }
                    textarea {
                        border-radius: 0 15px 15px 15px;
                        // width: 100%;
                        height: 160px;
                        font-size: 2em;
                        transition: border-color 0.3s; // Ajouter une transition pour une animation fluide
                        &:focus {
                            outline: none;
                        }
                    }
                }
                .send {
                    display: grid;
                    width: 100%;
                    .btn-send {
                        display: flex;
                        width: 30%;
                        justify-self: flex-end;
                        border: none;
                        justify-content: center;
                        background: #dbbe6e;
                        height: 60px;
                        border-radius: 16px;
                        color: #ffffff;
                        font-family: "Kodchasan-bold";
                        font-size: 1.4em;
                    }
                }
            }
        }
        .slogan {
            p {
                color: #ad9657;
                font-family: "Kodchasan-italic";
                font-size: 1.4em;
            }
        }
    }
}

@media screen and (min-width: 1024px) {
    .contact-page {
        display: flex;
        background: #ffffff;
        flex-wrap: wrap;
        width: 80,937%;   
        h1 {
            animation: haut 3s ease-in;
            height: 10%;
            display: flex;
            align-items: center;
            width: 100%;
            margin: 40px 0 0 8%;
            color: #333f4f;
            font-size: 2.5em;
            font-weight: 500;
        }
        .information-me {
            height: 65%;
            width: 32.813%;
            margin-left: 8%;
            margin-top: 20px;
            h2 {animation: haut 3s ease-in;
                color: #8197b8;
                font-family: "Kodchasan-regular";
                font-size: 2em;
            }
            .social-media-all {
                display: none;
            }
            h3 {
                animation: left-to-right 3s ease-in;
                display: flex;
                flex-direction: column;
                margin-top: 50px;
                color: #333f4f;
                font-family: "Kodchasan-regular";
                font-size: 2.1em;
                .description {
                    color: #ad9657;
                    font-size: 0.49em;
                }
            }
            .direct-contact {
                animation: left-to-right 3s ease-in;
                display: flex;
                margin-top: 20px;
                flex-direction: column;
                justify-content: space-around;
                height: 200px;
                a {
                    display: flex;
                    align-items: center;
                    p {
                        color: #333f4f;
                        margin-left: 5%;
                        font-family: "kodchasan-regular";
                    }
                }
            }
        }
        .form-container {
            height: 80%;
            width: 50%;
            form {
                display: flex;
                height: 100%;
                align-items: flex-start;
                justify-content: space-around;
                flex-wrap: wrap;
                .information {
                    width: 45%;
                    display: flex;
                    flex-direction: column;
                    // height: 130px;
                    &:nth-child(3) {
                        input {
                            border-radius: 0 15px 15px 15px;
                        }
                    }
                    &:nth-child(1) {
                        input {
                            border-radius: 0 15px 15px 15px;
                        }
                    }
                    &:nth-child(5) {
                        width: 95%;
                        height: 320px;
                    }
                    &:nth-child(4) {
                        align-items: flex-end;
                        input {
                            border-radius: 15px 0 15px 15px;
                        }
                    }
                    &:nth-child(2) {
                        align-items: flex-end;
                        input {
                            border-radius: 15px 0 15px 15px;
                        }
                    }

                    label {
                        font-size: 1em;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background: #333f4f;
                        border-radius: 15px 15px 0 0;
                        height: 52px;
                        color: #ffffff;
                        font-family: "kodchasan-regular";
                    }
                    .name {
                        width: 39%;
                    }
                    .company {
                        width: 40%;
                    }
                    .phone {
                        width: 50%;
                    }
                    .email {
                        width: 32%;
                    }
                    .message {
                        width: 30%;
                    }
                    input {
                        color: #333f4f;
                        font-family: "kodchasan-regular";
                        font-weight: 900;
                        background: #becadb;
                        border: none;
                        height: 60px;
                        width: 100%;
                        text-align: center;
                        &:focus {
                            outline: none;
                        }
                    }
                    textarea {
                        font-weight: 900;
                        padding: 10px;
                        color: #333f4f;
                        font-family: "kodchasan-regular";
                        height: 160px;
                        width: 100%;
                        border: none;
                        background: #becadb;
                        border-radius: 0 15px 15px 15px;
                        &:focus {
                            outline: none;
                        }
                    }
                }
                .send {
                    margin-top: 20px;
                    margin-bottom: 50px;
                    width: 20%;
                    height: 85px;
                    border-radius: 25px;
                    position: absolute;
                    bottom: 0;
                    margin-right: 10%;
                    right: 0;
                    input {
                        cursor: pointer;
                        font-size: 2em;
                        font-family: "Kodchasan-bold";
                        border-radius: 25px;
                        height: 100%;
                        width: 100%;
                        border: none;
                        background: #dbbe6e;
                        color: #ffffff;
                    }
                }
            }
        }
    }
    .slogan {
        animation: bas 3s ease-in;
        height: 10%;
        margin-left: 10%;
       bottom: 0;
       position: absolute;
       margin-bottom: 40px;
        
        p{color: #AD9657;
            font-family: "Kodchasan-regular";
            font-size: 1.5em;
    }
}
}