//mobile
@media screen and (max-width: 639px) {
    .mb-inside {
        display: flex;
        justify-content: flex-end;
        p {
            font-size: 0.7em;
            text-align: center;
        }
        .original-class {
            z-index: 3;
            position: fixed;
            background: #333f4f;
            width: 0%;
            height: 60vh;
            margin-top: 29px;
            transition: all 1s;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            right: 0;
            
            .croix {
                visibility: hidden;
            }
            nav {
                transition: opacity 0.1s ease;
                opacity: 0;
                // margin-top: 20px;
                height: 50vh;
                width: 0%;
                ul {
                    height: 100%;
                    width: 0%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-evenly;
                    align-items: center;
                }
            }
            a {
                width: 0%;
                color: #ffffff;
                text-align: center;
            }
            .mobile-languages {
                width: 100%;
                display: flex;
                justify-content: space-evenly;
                button {
                    background: #f1f1f1;
                    color: #707070;
                    border: none;
                }
            }
            .footer-m{
                transition: opacity 0.1s ease;
                opacity: 0;
            }
        }
        .modified-class {
            font-family: "Quango";
            right: 0;
            color: #ffffff;
            position: fixed;
            background: #333f4f;
            width: 64.7%;
            height: 60vh;

            transition: all 1s;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            margin-top: 29px;
            z-index: 3;
            nav {
                opacity: 1;
                transition: opacity 1.5s ease;
                // margin-top: 20px;
                height: 50vh;
                width: 100%;
                ul {
                    height: 100%;
                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-evenly;
                    align-items: center;
                    font-family: "Quango";
                    font-size: 1.5em;
                }
            }
            a {
                color: #ffffff;
                text-align: center;
            }
            .mobile-languages {
                width: 100%;
                display: flex;
                justify-content: space-evenly;

                button {
                    background: #f1f1f1;
                    color: #707070;
                    border: none;
                }
            }
            .footer-m {
                opacity: 1;
                transition: opacity 2s ease;
            }
        }
    }
}
@media screen and (min-width: 640px) {
    .mb-inside {
        display: none;
    }
}
