.animation-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: white; /* Couleur de fond initiale */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000; /* Pour s'assurer qu'elle est au-dessus de tout */
  transition: background 1s ease-in-out;
}
