.hidden {
  display: none;
}
.flex {
  display: flex;
}
.animation {
   border-radius: 100%;
  position: fixed;
  // top: 40%;
  // left: 50%;
  width: 100px;
  height: 100px;
  background-color: #445061;
  animation: expandAndCollapse 4s ease-in-out;
}

.animation .tache{
  display: none;
  position: fixed;
  // top: 50%;
  // left: 50%;
  // transform: translate(-50%, -50%);
  width: 100px;
 
  object-fit: cover;
 
}

@keyframes expandAndCollapse {
  0% {
    border-radius: 100%;
    width: 150%;
    height: 150vh;
   
   
  }
 
  
  100% {
  border-radius: 100%;
    width: 100px;
    height: 100px;
   
 
  }
}
.animation.finished{
  display: none;
}
.animation.finished.tache {
  display: block;
}
.img-tache{
  // position: absolute;
  width: 100%;
  height: 100%;
  scale: 2.5;
  object-fit: cover;
}
.logo-tache{
   position: fixed;
  width: 100px;
  top: 45%;
  left: 45%;
  animation: disparate 2s ease-in;
  
}
@keyframes bag {
  0%{
    width: 100px;
    
  }
 

  100%{
    
    width: 100%
   
  }
}
@keyframes disparate {
  0%{
    opacity: 1;
    
  }
 

  100%{
    
    opacity: 0;
   
  }
}

.big {
object-fit: cover;
  animation: bag 2s ease-in-out;
}
@media  screen and (max-width: 500px) {
  @keyframes bag {
    0%{
      width: 100px;
      height: 100px;
      
    }
   
  
    100%{
      height: 100vh;
      width: 100%
     
    }
  }

}