$sami: width(ul);
@media screen and (max-width: 639px) {
    .avis {
        background: #333f4f;
        display: flex;
        flex-direction: column;
        margin-top: 42px;
        align-items: center;
        // height: auto;
        // position: initial;
        // overflow-y: auto;
        .avis-client {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            margin-top: 25px;
            width: 90%;
            height: 75%;
            h2 {
                color: #fee8c0;
                font-family: "Workbench";
                font-size: 1.2em;
            }
            .all-avis {
                margin-top: 50px;
                margin-bottom: 30px;
                height: 90%;
                display: flex;
                justify-content: center;

                .ul {
                    height: 100%;
                    width: 95%;
                    overflow-y: scroll;
                    background: #48576d;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    scrollbar-width: none; // Masque la barre de défilement pour Firefox
                    -ms-overflow-style: none; // Masque la barre de défilement pour IE et Edge
                    border-radius: 15px;
                    // Masque la barre de défilement pour WebKit (Chrome, Safari, etc.)
                    &::-webkit-scrollbar {
                        display: none;
                    }
                    .li {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        .information-identity {
                            width: 90%;
                            margin-left: 8%;
                            margin-bottom: 10px;
                            margin-top: 15px;
                        }
                        p {
                            display: flex;
                            align-items: center;
                            font-size: 0.8em;
                        }
                        .text {
                            font-family: "Kodchasan-regular";
                            margin-top: 25px;
                            margin-left: 8%;
                            width: 90%;

                            font-size: 0.8em;
                        }
                        .identity {
                            font-family: "Kodchasan-regular";
                            color: #fee8c0;
                            font-size: 0.8em;
                        }
                        .rond {
                            display: flex;
                            border-radius: 100%;
                            background: #ffffff;
                            height: 25px;
                            width: 25px;
                            margin-right: 15px;
                        }
                        .triangle {
                            position: relative;
                            width: 95%; /* Largeur du trait */
                            height: 2px; /* Hauteur du trait */
                            background-color: #fee8c0; /* Couleur du trait */
                            // margin-bottom: 20px; /* Marge pour le triangle */
                            align-self: center;
                            margin-top: 5px;
                        }

                        .triangle:before {
                            content: "";
                            position: absolute;
                            bottom: -12px; /* Ajustez cette valeur pour positionner le triangle */
                            left: 50%; /* Position centrale horizontale */
                            transform: translateX(-50%); /* Pour centrer le triangle */
                            border-style: solid;
                            border-width: 10px 10px 0 10px; /* Taille et forme du triangle */
                            border-color: #fee8c0 transparent transparent transparent; /* Couleur du triangle */
                        }
                        .trait-avec-triangles {
                            align-self: center;
                            position: relative;
                            width: 95%; /* Ajustez selon vos besoins */
                            // height: 50px; /* Ajustez selon vos besoins */
                            border-top: 2px solid #fee8c0; /* Trait horizontal */
                        }

                        .trait-avec-triangles::before,
                        .trait-avec-triangles::after {
                            content: "";
                            position: absolute;
                            width: 0;
                            height: 0;
                        }

                        /* Triangle pointant vers le bas */
                        .trait-avec-triangles::before {
                            margin-top: 2px;
                            top: 100%;
                            left: 50%;
                            border: 10px solid transparent; /* Taille du triangle */
                            border-top-color: #fee8c0; /* Couleur du triangle */
                            transform: translateX(-50%);
                        }

                        /* Triangle pointant vers le haut */
                        .trait-avec-triangles::after {
                            margin-bottom: 4px;
                            bottom: 100%;
                            left: 50%;
                            border: 10px solid transparent; /* Taille du triangle */
                            border-bottom-color: #fee8c0; /* Couleur du triangle */
                            transform: translateX(-50%);
                        }
                        .triangle-haut {
                            position: relative;
                            width: 95%; /* Largeur du trait */
                            height: 2px; /* Hauteur du trait */
                            background-color: #fee8c0; /* Couleur du trait */

                            /* Création du triangle au-dessus */
                            &::before {
                                content: "";
                                position: absolute;
                                top: -12px; /* Ajustez cette valeur pour déplacer le triangle verticalement */
                                left: 50%;
                                transform: translateX(-50%);
                                border-width: 0 10px 10px 10px; /* Modifiez la valeur pour changer la taille du triangle */
                                border-style: solid;
                                border-color: transparent transparent #fee8c0 transparent;
                            }
                        }
                    }
                }
            }
        }
    }
}

