* {
    list-style-type: none;
    text-decoration: none;
    margin: 0;
    padding: 0;
}
body.transparent {
    background: transparent;
  }
  
  body.color {
    background: #445061;
  }
body {
    display: flex;
//   background: #445061;
   min-height: 100vh;
  // background: #445061;
}
@keyframes swing {
    0% {
        transform: rotate(0deg);
    }
    50% {
        transform: rotate(15deg);
    }
    100% {
        transform: rotate(0deg);
    }
}
.first,
.and {
    font-family: "Workbench", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    font-variation-settings:
        "BLED" 0,
        "SCAN" 0;
    color: #5bacb6;
}
.and {
    width: 25%;
}
@keyframes wave {
    0% {
        background-position: 0 0;
    }
    100% {
        background-position: -300% 100%;
    }
}
.second {
    font-family: "Pacifico", cursive;
    font-weight: 400;
    font-style: normal;
    color: #edd7ad;
    //
    // position: relative;
    // display: inline-block;
    transform: rotate(-45deg), translateY(-10%);
    transform-origin: 0 100%;
    // shape-outside: circle(50% at 100% 50%);
    // float: left;
    // padding: 10px;
    // display: inline-block;
    // animation: wave 1s ease-in-out infinite;

    font-size: 24px;
    font-weight: bold;
    //background: linear-gradient(to right, #007bff, #007bff 50%, transparent 50%, transparent);
    background-size: 200% 100%;
    animation: wave 2s infinite linear;
    display: inline-block;
    //color: white;
    // border-radius: 20px;
}
@font-face {
    font-family: "quango";
    src: url("../../../public/assets/police/quango/quango.bold.otf");
}
