font-family: "Workbench";
src: url("../../../public/assets/police/Workbench/Workbench-Regular-VariableFont_BLED\,SCAN.ttf");
}




@media screen and (min-width: 640px) and (max-width:1023px) {
    #root{
        display: flex;
        height: 100vh;
        width: 100%;
    }
    
}
@media screen and (max-width: 639px) {
    body {
        flex-direction: column;
       
    }
}
@media  screen  and (min-width : 1024px) {
    #root {
        // background: #445061;
        width: 100%;
        display: flex;
    }
    
}
@keyframes back {
    0%{
        background: transparent;
    }
    100%{
        background: #445061;
    }
}

