@media screen and (max-width: 639px) {
    
    .collaboration {
        
        display: flex;
        flex-direction: column;
        width: 90%;
        margin-top: 30px;
        height: 10%;
        justify-content: space-around;
        h2 {
            color: #FEE8C0;
            font-family: "Workbench";
            font-size: 1.2em
        }
    .collaboration-img {
        display: flex;
        width: 100%;
        justify-content: space-around;
        align-items: center;
        img{
            width: 15px;
            height: 15px;
            object-fit: cover;
        }
        .all-logo {
            display: flex;
            width: 80%;
            overflow-x: scroll;
            justify-content: space-around;
            scrollbar-width: none; // Masque la barre de défilement pour Firefox
            -ms-overflow-style: none; // Masque la barre de défilement pour IE et Edge
            border-radius: 15px;
            // Masque la barre de défilement pour WebKit (Chrome, Safari, etc.)
            &::-webkit-scrollbar {
                display: none;
            }
        .logo{
            background: transparent;
           width: 40%;
        //    height: 30px;
           border-radius: 10px;
            img{    
                width: 100px;
                height: 50px;
                object-fit: contain;
            }
        }
        }
    }
}
}
@media screen and (min-width:640px) and (max-width: 1023px) {
    .triangles {
        cursor: pointer;
        width: 0px;
        height: 0px;
        border-left: 15px solid transparent;
        border-right: 15px solid transparent;
        border-bottom: 15px solid #FEE8C0;
    }
    .n{
        rotate: 90deg;
    }
    .b{
        rotate: -90deg
    }
    .collaboration{
        display: flex;
        flex-direction: column;
        h2 {
            color: #FEE8C0;
            font-family: "Workbench";
            font-size: 2em;
            font-weight: 300;
           margin-left: 10%;
           width: 90%;
        }
        .collaboration-img{
            margin-bottom: 10px;
            margin-top: 35px;
            display: flex;
            align-items: center;
            justify-content: space-around;
            .all-logo{
                align-items: center;
                width: 80%;
                display: flex;
                overflow-x: scroll;
                scrollbar-width: none; // Masque la barre de défilement pour Firefox
                -ms-overflow-style: none; // Masque la barre de défilement pour IE et Edge
                border-radius: 15px;
                // Masque la barre de défilement pour WebKit (Chrome, Safari, etc.)
                &::-webkit-scrollbar {
                    display: none;
                }
                
                .logo{
                    margin-right: 50px;
                    display: flex;
                    flex: 0 0 15vw;
                    border-radius: 15px;
                    img{
                        object-fit: cover;
                        width: 100%;
                        height: 100%;
                        border-radius: 15px;
                    }
                }
            }
        }
    }
    
}

