@media screen and (max-width: 639px){

.menu-burger {
  cursor: pointer;
  position: fixed;
    display: flex;
    flex-direction: column;
    height: 30px;
    justify-content: space-around;
    position: fixed;
    width: 8%;
  top: 0;
  right: 0;
  z-index: 3;
  margin-top: 7px;
  margin-right: 10px;
    span {
        height: 3px;
        width: 100%;
        background: #FFFFFF;
        border-radius: 10px;
    }
}
.croix {
  cursor: pointer;
  width: 30px; /* Largeur de la croix */
  height: 30px; /* Hauteur de la croix */
  position: relative;
  background: #FFFFFF;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 5.8%;
}

.croix span {
  position: absolute;
  width: 80%;
  height: 2px; /* Épaisseur de la ligne */
  background: #707070;
}

.croix span:first-child {
  transform: rotate(45deg); /* Rotation de 45 degrés */
}

.croix span:last-child {
  transform: rotate(-45deg); /* Rotation de -45 degrés */
}

}
.menu-burger{
  span {
    background: #FFFFFF;
  }
}