@media screen and (max-width: 639px) {
    .form-container {
display: flex;
justify-content: center;
  
    form {
        width: 85%;
        display: flex;
        height: 800px;
        flex-direction: column;
        justify-content: space-around;
        .information {
            display: flex;
            flex-direction: column;
        }
        label {
            display: flex;
            align-items: center;
            justify-content: center;
            background: #333f4f;
            color: #ffffff;
            border-radius: 15px 15px 0px 0px;
            text-align: center;
            height: 30px;
        }
        .name , .email{
            width: 22.5%;
        }
        .company{
            width: 25%;
        }
        .phone, .message {
            width: 35%;
        }
        

        input {
            background: #becadb;
            border-radius: 0px 15px 15px 15px;
            height: 45px;
            border: none;
        }
        textarea {
            border: none;
            background: #becadb;
            border-radius: 0px 15px 15px 15px;
            height: 200px;
            padding: 10px;
        }
        .btn-send {
            background: #dbbe6e;
            color: #ffffff;
            border-radius: 16px 16px 16px 16px;
            width: 40.315%;
            border: none;
            align-self: flex-end;
            font-family: "Kodchasan-bold";
        }
    }
}
}
