@media screen and (max-width: 639px) {
    .realisation-page {
        h1 {
            font-weight: lighter;
        }
        .original-class {
            top: 0;
            margin-top: 43px;
        }
        .modified-class {
            top: 0;
            margin-top: 43px;
        }
        display: flex;
        flex-direction: column;
        margin-top: 5px;
        align-items: center;
        justify-content: space-around;
      
        // height: 200vh;
        .realisation {
            display: none;
        }
        .mobile-realisation {
            height: 80%;
            width: 100%;
overflow-y: scroll;
scrollbar-width: none; // Masque la barre de défilement pour Firefox
-ms-overflow-style: none; // Masque la barre de défilement pour IE et Edge

// Masque la barre de défilement pour WebKit (Chrome, Safari, etc.)
&::-webkit-scrollbar {
    display: none;
}
            align-items: center;
            // overflow-x: scroll;
            .direction, .direction-inv {
                display: none;
            }
            .all-card {
                margin-right: 15px;
                align-items: center;
                overflow-x: auto;
                scrollbar-width: none; // Masque la barre de défilement pour Firefox
                -ms-overflow-style: none; // Masque la barre de défilement pour IE et Edge
              
                // Masque la barre de défilement pour WebKit (Chrome, Safari, etc.)
                &::-webkit-scrollbar {
                    display: none;
                }
                display: flex;
                height: 80%;
                // width: 100vw;
                flex-direction: row; /* Alignement horizontal des éléments enfants */

                .realisation-card {
                    height: 74.225%;
                    margin-left: 29px;
                    width: 75.315%;
                    border-radius: 15px;
                    flex: 0 0 auto;
                    //margin-right: 25px;
                    img {
                        border-radius: 15px;
                        object-fit: cover;
                        width: 100%;
                        height: 100%;
                        cursor: pointer;
                    }
                }
                .card-inside {
                    display: none;
                }
            }
            .all-card-modif {
                display: flex;
                flex-direction: column;
                align-items: center;
                .card-inside {
                    height: 270px;
                    width: 85.315%;
                    .image-inside {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
                .caroussel {
                    margin-bottom: 45px;
                    margin-top: 45px;
                    display: flex;
                    width: 85%;
                    justify-content: space-evenly;
                    .p-visible {
                        display: flex;
                        width: 30%;
                        justify-content: space-around;
                        align-items: center;
                        text-align: center;
                        font-size: 0.7em;
                        .p-next{
                            order: 2;
                        }
                        img {
                            // height: 45px;
                            width: 15%;
                            object-fit: cover;
                        }
                        .img-next{
                            order: 1;
                        }
                    }
                    .p-invisible {
                        width: 30%;
                    }
                }
            }
        }

        .card-modif {
            display: none;
        }
    }
    .p-inv {
        display: none;
    }
    .p-visible {
        display: flex;
        cursor: pointer;
    }
}

@media screen and (min-width: 640px) and (max-width: 1023px) {
    .realisation{
        display: none;
    }
    .direction-inv {
        display: none;
    }
    .realisation-page {
        display: flex;
        flex-direction: column;
        h1 {
            margin: 10%;
            font-size: 2em;
            font-weight: 300;
        }
        .mobile-realisation {
            display: flex;
            flex-direction: column;
            align-items: center;
            height: 85vh;
            .pre {
                rotate: 90deg;
            }
            .suiv {
                rotate: 90deg;
            }
            .all-card {
                display: flex;
                flex-direction: column;
                align-items: center;
                overflow-y: scroll;
                scrollbar-width: none; // Masque la barre de défilement pour Firefox
                -ms-overflow-style: none; // Masque la barre de défilement pour IE et Edge
              
                // Masque la barre de défilement pour WebKit (Chrome, Safari, etc.)
                &::-webkit-scrollbar {
                    display: none;
                }
                height: 75vh;
                width: 100%;

                .realisation-card {
                    display: flex;
                    width: 93.675%;
                    border-radius: 25px;
                    height: 581px;
                    margin-bottom: 30px;
                    img {
                        width: 100%;
                        border-radius: 25px;
                        object-fit: cover;
                        height: 100%;
                    }
                }
                .caroussel {
                    display: none;
                }
                .card-inside {
                    display: none;
                }
            }
            .all-card-modif {
                display: flex;
                flex-direction: column;
                // height: 75vh;
                overflow-y: scroll;
                scrollbar-width: none; // Masque la barre de défilement pour Firefox
                -ms-overflow-style: none; // Masque la barre de défilement pour IE et Edge
              
                // Masque la barre de défilement pour WebKit (Chrome, Safari, etc.)
                &::-webkit-scrollbar {
                    display: none;
                }
                width: 100%;
                align-items: center;
                .card-inside {
                    display: flex;
                    width: 90%;
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
                .card-modif {
                    display: none;
                }
                .caroussel {
                    // position: relative;
                    // right: 0;
                    // bottom: 0;
                    margin-top: 20px;
                    display: flex;
                    align-items: center;
                    justify-content: space-around;
                    width: 90%;
                    .visibility {
                        visibility: hidden;
                    }
                    .invisibility {
                        visibility: visible;
                    }
                    .p-visible {
                        // visibility: hidden;
                        display: flex;
                        flex-direction: row-reverse;
                        align-items: center;
                        justify-content: space-around;
                        text-align: center;
                        width: 30%;
                        img {
                            width: 20%;
                        }
                        p {
                            width: 40%;
                            text-align: center;
                        }
                    }
                }
            }
        }
    }
}

