.confirmation-section {
    width: 100%;
    background: #ffff !important;
    img{
        width: 75px;
        left: 60%;
        position: absolute;
        top: 35%;
        z-index: 3
    }
    .confirmation-div {
        display: flex;
        background: #9db9e0;
        width: 49.7%;
        height: 215px;
        left: 40%;
        position: absolute;
        top: 40%;
        border-radius: 15px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        p{
            font-family: "Kodchasan-bold";
            font-size: 1.2em;
            text-align: center;
        }
       
    }
}
